import React, { useEffect, useState } from 'react';
import { Button, Layout, Table } from 'antd';
import Form from './UserForm';
import getBaseUrl from './utils';
const { Content } = Layout;

const toSentenceCase = str => {
  const spaced = str.replace(/([A-Z])/g,' $1');
  return spaced.charAt(0).toUpperCase() + spaced.slice(1);
};

const baseUrl = getBaseUrl();

const Admin = () => {
  const [userData, setUserData] = useState([]);
  const [showModal, setShowModal] = useState(false);

  const fetchData = async() => {
    const raw = await fetch(`${baseUrl}/users`);
    const data = await raw.json();
    setUserData(data);
  };

  useEffect(() =>  {
    fetchData();
  }, []);

  const tableColumns = userData && userData.length && [
    ...Object.keys(userData[0]).filter(
      (col) => !['imgUrl', 'score'].includes(col)).map((col, index) =>({
       key: index,
       dataIndex: col,
       title: toSentenceCase(col),
      })),
 ];

 const onSubmit = async (values) => {
  await fetch(`${baseUrl}/users`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(values),
  });
  setShowModal(false);
  fetchData();
}


  return (
    <Content>
      <Table columns={tableColumns} dataSource={userData} />
      <Button
        type="primary"
        onClick={() => setShowModal(true)}
      >
        Add a new user
        </Button>
      <Form onSubmit={onSubmit} visible={showModal} onCancel={() => setShowModal(false)} />
    </Content>
  );
}

export default Admin;