import React from 'react';
import { Modal } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';

const { confirm } = Modal;

const DeleteConfirmation = ({ onSubmit, onCancel, selectedUser, isVisible}) => (
  <Modal
    title={`Remove ${selectedUser && selectedUser.firstName}`}
    visible={isVisible}
    okText="Yes"
    onCancel={onCancel}
    onOk={() => {
      onSubmit();
      onCancel();
    }}
    okButtonProps={{ danger: true }}
    centered
    >
      <p>
        {`Are you sure you want to remove ${selectedUser && selectedUser.firstName} from the tracker? Their jokes will also be deleted as a consequence of this.`}
      </p>
    </Modal>
)


export default DeleteConfirmation;